.container {
    height: 100vw;
    padding: 20px;
  
    background: #83a4d4;
    background: linear-gradient(to left, #96cee0, #d8e1ee);
  
    color: #171212;
  }
  
  .headline-primary {
    font-size: 48px;
    font-weight: 300;
    letter-spacing: 2px;
  }
  
  .item {
    display: flex;
    align-items: center;
    padding-bottom: 5px;
  }
  
  .item > span {
    padding: 0 5px;
    white-space: nowrap;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  
  .item > span > a {
    color: inherit;
  }
  
  .button {
    background: transparent;
    border: 1px solid #171212;
    padding: 5px;
    cursor: pointer;
  
    transition: all 0.1s ease-in;
  }
  
  .button:hover {
    background: #171212;
    color: #ffffff;
  }
  
  .button:hover > svg > g {
    fill: #ffffff;
    stroke: #ffffff;
  }
  
  .button_small {
    padding: 5px;
  }
  
  .button_large {
    padding: 10px;
  }
  
  .search-form {
    padding: 10px 0 20px 0;
    display: flex;
    align-items: baseline;
  }
  
  .label {
    border-top: 1px solid #171212;
    border-left: 1px solid #171212;
    padding-left: 5px;
    font-size: 24px;
  }
  
  .input {
    border: none;
    border-bottom: 1px solid #171212;
    background-color: transparent;
  
    font-size: 24px;
  }